import objectKeys from './objectKeys';

/**
 *  key  type
 * @method _separateTypeKey
 * @param  {String}         key 
 * @return {Object}             
 *                              {k: 'content', t: '%s'}
 */
function _separateTypeKey(key) {
  const matches = (key || '').match(/(\w+)(%\w)$/i);
  const tk = {
    k: key,
  };
  if (matches) {
    tk.k = matches[1];
    tk.t = matches[2];
  }
  return tk;
}
/**
 * 
 */
function __superToString(content) {
  let str = content;
  if (typeof content === 'object') {
    str = JSON.stringify(content);
  } else {
    str = `${content}`;
  }
  return str;
}
/**
 * 1610
 * @method __h2dColor
 * @param  {String}   hex 16
 * @return {Number}       10
 */
function __h2dColor(hex) {
  let dec = `${hex}`;
  // #
  if (dec.indexOf('#') === 0) {
    dec = dec.substr(1);
  }
  // 36
  if (dec.length === 3) {
    dec = dec.replace(/(.)/g, '$1$1');
  }
  dec = parseInt(dec, 16);
  if (isNaN(dec)) {
    console.error(`${hex} is invalid hex color.`);
  }
  return dec;
}
/**
 *  base64 native 
 * @method __removeBase64Head
 * @param  {String}           base64 
 * @return {String}                  
 */
function __removeBase64Head(base64) {
  if (typeof base64 === 'string') {
    base64 = base64.replace(/^data:(\/|\w|\-|\.)+;base64,/i, '');
  }
  return base64;
}
/**
 * 
 * @method toType
 * @param  {String} type  
 *                        %s()
 *                        %c(1610)
 *                        %h(1016)
 *                        %b( base64 )
 *                        %a{mimeType}( base64 )
 *                        %d()
 *                        %f()
 * @param  {any} value 
 * @return {any}       
 */
export function toType(type, value) {
  if (type === '%s') {
    value = __superToString(value);
  } else if (type === '%c') {
    value = __h2dColor(value);
  } else if (type === '%b') {
    value = __removeBase64Head(value);
  } else if (type === '%d') {
    value = parseInt(value, 10);
  } else if (type === '%f') {
    value = parseFloat(value);
  }
  return value;
}
/**
 * 
 * @method _mapping
 * @param  {Object}  tObj 
 * @param  {Object}  map {content: 'text'}
 *                        sObj.content  tObj.text
 *                        tObj  content 
 *                        content  sKeytext  tKey
 *                        map (:) to
 *                        {content to text}
 *                        tKey  %s 
 *                         tObj   tKey 
 *                       
 *                         
 *                        JSBridge.call('alert',{message: 12345})
 *
 * @param  {Object} sObj 
 * @return {Object}      tObj
 */
export function mapping(tObj, map, sObj) {
  let typeKey;
  sObj = sObj || {};
  objectKeys(map).forEach((sKey) => {
    let tKey = map[sKey];
    typeKey = _separateTypeKey(tKey);
    //  key
    tKey = typeKey.k;
    //  value  undefined  key
    if (tKey !== undefined && ( //  key 
      sKey in tObj || sKey in sObj) // 
        && tObj[tKey] === undefined // 
    ) {
      // sKey  sObj  tObj sObj  tObj
      //  sObj[sKey]  tObj[sKey]
      //  type 
      tObj[tKey] = toType(typeKey.t, sObj[sKey] === undefined ? tObj[sKey] : sObj[sKey]);
      //  tObj  sKeytKey  sKey 
      if (tKey !== sKey) {
        delete tObj[sKey];
      }
    }
  });
  return tObj;
}
export function apCallback(params = {}, res) {
  const { complete } = params;
  const { success } = params;
  const { fail } = params;

  if (res && res.error) {
    if (fail) {
      fail(res);
    }
  } else if (success) {
    success(res);
  }
  if (complete) {
    complete(res);
  }
}
